@import url(https://fonts.googleapis.com/css?family=Anton|Work+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Merriweather:400,900|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}a{cursor:pointer}html,body{scroll-behavior:smooth;background:#F7F1EF;color:#211E32;min-width:345px;font-family:"Merriweather , Open Sans";font-size:16px}.laptop{position:absolute;top:-10%;right:0;opacity:.6}header,section{overflow-x:hidden}header{display:flex;justify-content:center}header .row .col-md-7{padding:10vmin 1vmin;font-family:"Merriweather",sans-serif;color:#3f3d56}header .container .col-md-7{position:relative}header .container .col-md-7 h1{font-size:8vmin;font-size:4rem;font-weight:bold;line-height:1;padding:0.1em 0em}header .container .col-md-7 .motto{font-size:18px;font-weight:600;padding:0.1em 0em}header .container .col-md-7 .cta-btn{border-radius:30px;font-weight:bold;background-color:#8580fd;color:whitesmoke}header .container .col-md-7 .cta-btn:hover{opacity:.8;-webkit-transform:scale(1.04);transform:scale(1.04);transition:all .4s ease-in-out}.top-nav{margin-top:6rem}.top-nav h2{font-weight:700;font-size:36px;margin-bottom:1rem}.top-nav li{font-size:20px;list-style:none;display:inline;padding-right:20px;position:relative}.top-nav li a{text-decoration:none;color:#211E32}.link::after{content:'';display:none;position:absolute;top:24px;left:0;height:3px;width:80%;background:#c33764;border-bottom:3px solid red}.link:hover::after{display:inline}.projects-section h1{opacity:0.2;font-weight:700;font-size:48px;margin-bottom:2rem}.proj-card{margin-bottom:10px}.proj-card h2{font-family:"Anton",sans-serif;font-size:30px;margin-bottom:1.4rem}.proj-card p{font-family:"Work Sans",sans-serif;font-family:"Open Sans",sans-serif;font-size:18px;padding-right:10rem}.view{background:#211E32;color:#fff;font-family:"Anton",sans-serif;font-size:16px;letter-spacing:.5px;padding:5px 20px;border-radius:10px;border:none;outline-style:none}.view:hover{color:#06808D;background:rgba(33,30,50,0.822);transition:all .2s ease-in-out}.proj-rect{border-radius:15px}.proj-rect img{width:90%;min-width:280px;border-radius:10px}.contact-section{padding:0 8rem;margin-bottom:4rem}.contact-section h1{opacity:0.2;font-weight:700;font-size:48px;margin-bottom:2rem}.contact{background:#211E32;color:white;border-radius:30px;padding:4rem 6rem}.contact .top-line{display:flex;align-items:center;margin-bottom:2rem}.contact h2{font-family:"Work Sans",sans-serif;font-size:24px;display:inline-block;margin-right:6rem}.contact span{display:inline-block;justify-items:center;align-items:center}.contact span a{color:white}.contact span a:hover{color:#329AF0}.contact span i{font-size:24px;margin-right:10px}.contact .form-name input{width:50%;height:38px}.contact .form-email input{width:75%;height:40px}.contact label{display:block;font-family:"Anton",sans-serif;font-size:24px}.contact input,.contact textarea{border-radius:10px;border:none;outline:none;margin-bottom:2rem;font-family:"Work Sans",sans-serif;font-size:16px;padding-left:8px}.contact textarea{width:100%;height:160px}.contact button{font-family:"Anton",sans-serif;background:#FFFFFF;border-radius:10px;padding:4px 36px;outline:none;border:none}.contact button:hover{color:white;background-color:#3f3d56;-webkit-transform:translateY(2px);transform:translateY(2px)}footer{overflow:hidden;background-image:url(/static/media/wave.8f31634a.svg);background-repeat:no-repeat;background-size:cover;height:38vh}.wave{position:absolute;overflow-x:hidden}

@media only screen and (max-width: 991px){header .container .col-md-7 h6{position:static}.contact-section{padding:0 2rem}.contact{padding:4rem 2rem}}@media only screen and (max-width: 768px){.laptop{display:none}header .navbar-brand{padding-left:1rem}header .row .col-md-7{padding-bottom:0vmin}header .row .col-md-5{padding:4vmin 2vmin}header .row .col-md-5 img{width:60%}.proj-card p{padding-right:2rem}.contact-section{padding:0 1rem}.top-line{flex-direction:column}.top-line h2{font-size:20px;margin-right:2rem}}@media only screen and (max-width: 375px){.contact-section{padding:0}.top-line h2{margin-right:0rem}}

